import React from 'react'

import classes from './Presenter.module.css'

const Presenter = props => {
	return (
		<div className={`${classes.projects} ${props.className ? props.className : ''}`}>
			<div>
				<img src={props.img} alt={props.alt} />
			</div>
			<a
				href={props.link}
				rel="noreferrer noopener"
				target="_blank"
				className={classes.projectInfo}>
					{props.title}
			</a>
		</div>
	)
}

export default Presenter
