import React from 'react'

import classes from './SocialLinks.module.css'

import fiverr from '../../Assets/Images/icons/fiverr.svg'
import instagram from '../../Assets/Images/icons/instagram.svg'
import gmail from '../../Assets/Images/icons/gmail.png'
import github from '../../Assets/Images/icons/github.svg'
import facebook from '../../Assets/Images/icons/facebook.svg'
import youtube from '../../Assets/Images/icons/youtube.svg'

import links from '../../Assets/Links/Links'

const SocialLinks = () => {
	const contactLinks = [
		{
			name: "YouTube",
			url: links.yt,
			img: youtube
		},

		{
			name: "Fiverr",
			url: links.fiverr,
			img: fiverr
		},

		{
			name: "Instagram",
			url:links.ig,
			img: instagram
		},

		{
			name: "Email",
			url: links.email,
			img: gmail
		},

		{
			name: "Facebook",
			url: links.fb,
			img: facebook
		},

		{
			name: "Github",
			url: links.github,
			img: github
		}
	]

	return (
		<div className={`${classes.container} section`}>
			<div className={classes.linksContainer}>
				{
					contactLinks.map((link, i) => {
						return (
							<div key={i} className={`${classes.linkWrapper} ${i === contactLinks.length - 1 ? classes.last : ''}`}>
							<span className={classes.linkName}>{link.name}</span>
							<div className={classes.link}>
								<a
									title={link.name}
									target="_blank"
									rel="noreferrer noopener"
									href={link.url}>
									<img src={link.img} alt={link.name} className={classes.icon} />
								</a>
							</div>
							</div>
						)
					})
				}
			</div>
		</div>
	)
}

export default SocialLinks
