import React from 'react'

import Presenter from '../../Components/Presenter/Presenter'

import videoData from '../../Assets/YTVideo/YTVideo'
import classes from './YTVideos.module.css'
import links from '../../Assets/Links/Links'

const YTVideos = () => {
	return (
		<div className={`${classes.container} section`}>
			<h2 className={classes.heading}>My YouTube videos :</h2>
			<p className={classes.decription}>
				I upload tutorials on web development and some interesting JavaScript and React projects in&nbsp;
				<a target="_blank" rel="noreferrer noopener" className={`link`} href={links.yt}>
					YouTube
				</a>
				. On the way will learn some other skills
				as well.<br />
				So make sure you&nbsp;
				<a target="_blank" rel="noreferrer noopener" className={`link`} href={links.yt}>
					subscribe
				</a>&nbsp;to the channel.
			</p>
			<div className={classes.thumbnailsContainer}>
				{
					videoData.map((data, i) => {
						return (
							<Presenter
								key={i}
								img={`https://img.youtube.com/vi/${data.id}/hqdefault.jpg`}
								alt={data.name}
								title={data.name}
								className={classes.thumbnail}
								link={`https://youtube.com/watch?v=${data.id}/`} />
						)
					})
				}
			</div>
		</div>
	)
}

export default YTVideos
